/* for way 3 */
@import url("https://fonts.googleapis.com/css2?family=Oxygen:wght@300&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
}

body {
  height: 65vh;
  padding: 4% 7%;
  font-family: "Oxygen", sans-serif;
  width: 100%;
  background-color: #2c3c40;
  display: flex;
  justify-content: center;
}

header {
  background-color: #70797c;
  height: 450px;
  width: 100%;
  border-radius: 15px;
  box-shadow: -2px 1px 24px -1px rgba(0, 0, 11, 1);
}

.calculator-container {
  margin-top: 45px;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  gap: 15px;
  text-align: center;
}

.display-header {
  display: block;
  width: 483px;
  padding: 10px 35px;
  height: 50px;
  border: none;
  margin-left: 32px;
  margin-bottom: 10px;
  background-color: #fff;
  box-shadow: -2px 5px 19px -4px rgba(255, 255, 255, 1);
  outline: none;
}