.calculator-container {
  margin-top: 45px;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  gap: 15px;
  text-align: center;
}

input {
  flex-wrap: wrap;
  width: 110px;
  height: 300px;
  padding: 10px 35px;
  height: auto;
  cursor: pointer;
  margin: 0 5px;
  background-color: #2c3c40;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  box-shadow: -2px 5px 23px -4px rgba(0, 0, 0, 0.75);
}

input:active {
  transform: scale(1.1);
  box-shadow: -2px 5px 23px -4px rgba(19, 19, 19, 0.75);
}
